export default{
	data(){
		return{
			myData:[],
			sessionId:'',
			tabCurrentIndex: 0,
			navList: [
				{
					state: 0,
					text: '人数未满',
					orderList: []
				},
				{
					state: 1,
					text: '人数已满',
					orderList: []
				},
				{
					state: 2,
					text: '已完成',
					orderList: []
				},
				{
					state: 3,
					text: '已取消',
					orderList: []
				}
			],
		}
	},
	onLoad(option) {
		this.tabCurrentIndex = 0;
		this.sessionId = option.sessionId
		this.init()
	},
	methods:{
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/getActivities',
				withCredentials:true,
				data:{
					sessionId:this.sessionId
				},
				success: (res) => {
					if(res.data.stat == 1){
						this.myData = res.data.data
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		},
		// 提示先登录
		stopSwitch(){
			uni.showToast({
				title:'请先登录',
				icon:'none'
			})
		},
		
		// 跳转至登录页面
		gotoLogin(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		
		// 跳转至详情页
		gotoDetail(item){
			var id = item.id
			uni.navigateTo({
				url:'../activityDetail/activityDetail?id='+id
			})
		},
	},
}
